import { createApp } from 'vue'
import App from './App.vue'
import store from './store'
import { Button, Cascader, Input, Radio, Select, Upload, Checkbox, Tabs, DatePicker } from 'ant-design-vue';

createApp(App).use(Button).use(Cascader).use(Input).use(Radio).use(Select).use(Upload).use(Checkbox).use(Tabs).use(DatePicker).use(store).mount('#app')
