<template>
  <div class="hello">
    <a-tabs @change="callback" size="large">
      <a-tab-pane key="1" tab="个人">
        <div class="form">
          <h5>信息录入</h5>
          <div class="form_data">
            <h6>贷款人姓名</h6>
            <a-input v-model:value="information.nickname" />
          </div>
          <div class="form_data">
            <h6>地区</h6>
            <a-cascader
              :options="map"
              placeholder="请选择地区"
              @change="onChange"
              size="large"
              class="cascader"
            />
          </div>
          <div class="form_data">
            <h6>贷款人手机号</h6>
            <a-input v-model:value="information.phone" />
          </div>
          <div class="form_data">
            <h6>贷款人身份证号</h6>
            <a-input v-model:value="information.idcard" />
          </div>
          <div class="form_data">
            <h6>是否为贫困户</h6>
            <a-radio-group
              name="radioGroup"
              v-model:value="information.is_poor"
            >
              <a-radio value="1"> 是 </a-radio>
              <a-radio value="0"> 否 </a-radio>
            </a-radio-group>
          </div>
          <div class="form_data">
            <h6>信用评级</h6>
            <a-select
              v-model:value="information.credit_rating"
              style="width: 100%"
              ref="select"
            >
              <a-select-option
                v-for="(item, i) in creditlist"
                :key="i"
                :value="item.name"
              >
                {{ item.name }}
              </a-select-option>
            </a-select>
          </div>
          <div class="form_data">
            <h6>借款日</h6>
            <a-date-picker
              v-model:value="information.loan_starttime"
              @change="information_starttime"
            />
          </div>
          <div class="form_data">
            <h6>到期日</h6>
            <a-date-picker
              v-model:value="information.loan_endtime"
              @change="information_endtime"
            />
          </div>
          <div class="form_data">
            <h6>还款日</h6>
            <a-date-picker
              v-model:value="information.payback_time"
              @change="information_payback"
            />
          </div>
          <div class="form_data">
            <h6>贷款状态</h6>
            <a-radio-group
              name="radioGroup"
              v-model:value="information.loan_status_type"
            >
              <a-radio value="正常"> 正常 </a-radio>
              <a-radio value="逾期"> 逾期 </a-radio>
              <a-radio value="不良"> 不良 </a-radio>
            </a-radio-group>
          </div>
          <div class="form_data" v-if="ifoverduereason_information == true">
            <h6>逾期原因</h6>
            <a-radio-group
              :style="radioStyle"
              :options="overduereasonlist"
              v-model:value="information.overduereason"
            />
          </div>
          <div class="form_data">
            <h6>贷款金额</h6>
            <a-input
              v-model:value="information.loan_price"
              placeholder="单位：元"
            />
          </div>
          <div class="form_data">
            <h6>年利率</h6>
            <a-input v-model:value="information.year_interest_rate" />
          </div>
          <div class="form_data">
            <h6>贴息利率</h6>
            <a-input v-model:value="information.discount_rate" placeholder="" />
          </div>
          <div class="form_data">
            <h6>应贴息金额(元)</h6>
            <a-input v-model:value="information.discount_price" />
          </div>
          <div class="form_data">
            <h6>产业扶持金额(元)</h6>
            <a-input v-model:value="information.supportprice" />
          </div>
          <div class="form_data">
            <h6>贷款用途</h6>
            <a-cascader
              :options="industry"
              placeholder="请选择产业"
              @change="industrybtn"
              size="large"
              class="cascader"
            />
          </div>
          <div class="form_data">
            <h6>贷款类型</h6>
            <a-radio-group
              name="radioGroup"
              v-model:value="information.loan_type"
            >
              <a-radio value="新增"> 新增 </a-radio>
              <a-radio value="续贷"> 续贷 </a-radio>
            </a-radio-group>
          </div>
          <div class="form_data">
            <h6>贷款银行</h6>
            <a-select
              v-model:value="information.loan_bank"
              style="width: 100%"
              ref="select"
            >
              <a-select-option
                v-for="(item, i) in Loan_banklist"
                :key="i"
                :value="item.bank_name"
              >
                {{ item.bank_name }}
              </a-select-option>
            </a-select>
          </div>
          <div class="form_data">
            <h6>贷款银行详细名称</h6>
            <a-textarea
              v-model:value="information.loan_bank_desc"
              :auto-size="{ minRows: 4 }"
            />
          </div>
          <div class="form_data">
            <h6>是否参加过信用等级评定</h6>
            <a-radio-group
              :options="isjoincredit"
              v-model:value="information.is_joincredit"
            />
          </div>
          <div class="form_data">
            <h6>是否购买过贷款保证保险</h6>
            <a-radio-group
              name="radioGroup"
              v-model:value="information.is_safe"
            >
              <a-radio value="否"> 否 </a-radio>
              <a-radio value="是"> 是 </a-radio>
            </a-radio-group>
          </div>
          <div class="form_data">
            <h6>委托证明</h6>
            <a-upload
              action="http://jinrongapi.yooobei.com/Loan/uploads"
              list-type="picture"
              multiple="true"
              v-model:fileList="fileList"
              @change="handleChange"
            >
              <a-button> <upload-outlined /> upload </a-button>
            </a-upload>
          </div>
          <div class="form_btn">
            <a-button
              @click="primary_personal"
              style="margin-right: 20px"
              type="primary"
              >确认</a-button
            >
            <a-button @click="Reset">重置</a-button>
          </div>
        </div>
      </a-tab-pane>
      <a-tab-pane key="2" tab="企业" force-render>
        <div class="form">
          <h5>信息录入</h5>
          <div class="form_data">
            <h6>地区</h6>
            <a-cascader
              :options="map"
              placeholder="请选择地区"
              @change="onChangx"
              size="large"
              class="cascader"
            />
          </div>
          <div class="form_data">
            <h6>企业名称</h6>
            <a-input v-model:value="enterprise.nickname" />
          </div>
          <div class="form_data">
            <h6>统一社会信用代码</h6>
            <a-input v-model:value="enterprise.credit_code" />
          </div>
          <div class="form_data">
            <h6>法定代表人</h6>
            <a-input v-model:value="enterprise.deputy_username" />
          </div>
          <div class="form_data">
            <h6>联系电话</h6>
            <a-input v-model:value="enterprise.phone" />
          </div>
          <div class="form_data">
            <h6>注册地址</h6>
            <a-input v-model:value="enterprise.address" />
          </div>
          <div class="form_data">
            <h6>股东人数</h6>
            <a-input
              v-model:value="enterprise.staff_num"
              placeholder="请输入主要股东数量"
            />
          </div>
          <div class="form_data">
            <h6>是否为龙头企业</h6>
            <a-radio-group
              name="radioGroup"
              v-model:value="enterprise.is_faucet"
            >
              <a-radio value="0"> 否 </a-radio>
              <a-radio value="1"> 是 </a-radio>
            </a-radio-group>
          </div>
          <div class="form_data">
            <h6>贷款金额</h6>
            <a-input v-model:value="enterprise.loan_price" />
          </div>
          <div class="form_data">
            <h6>借贷日</h6>
            <a-date-picker
              v-model:value="enterprise.loan_starttime"
              @change="enterprise_starttime"
            />
          </div>
          <div class="form_data">
            <h6>到期日</h6>
            <a-date-picker
              v-model:value="enterprise.loan_endtime"
              @change="enterprise_endtime"
            />
          </div>
          <div class="form_data">
            <h6>还款日</h6>
            <a-date-picker
              v-model:value="enterprise.payback_time"
              @change="enterprise_payback"
            />
          </div>
          <div class="form_data">
            <h6>贷款状态</h6>
            <a-radio-group
              name="radioGroup"
              v-model:value="enterprise.loan_status_type"
            >
              <a-radio value="正常"> 正常 </a-radio>
              <a-radio value="逾期"> 逾期 </a-radio>
              <a-radio value="不良"> 不良 </a-radio>
            </a-radio-group>
          </div>
          <div class="form_data" v-if="ifoverduereason_enterprise == true">
            <h6>逾期原因</h6>
            <a-radio-group
              :style="radioStyle"
              :options="overduereasonlist"
              v-model:value="enterprise.overduereason"
            />
          </div>
          <div class="form_data">
            <h6>年利率</h6>
            <a-input v-model:value="enterprise.year_interest_rate" />
          </div>
          <div class="form_data">
            <h6>贴息利率</h6>
            <a-input v-model:value="enterprise.discount_rate" placeholder="" />
          </div>
          <div class="form_data">
            <h6>应贴息金额(元)</h6>
            <a-input v-model:value="enterprise.discount_price" />
          </div>
          <div class="form_data">
            <h6>贷款用途</h6>
            <a-cascader
              :options="industry"
              placeholder="请选择产业"
              @change="enterprisebtn"
              size="large"
              class="cascader"
            />
          </div>
          <div class="form_data">
            <h6>产业扶持金额(元)</h6>
            <a-input v-model:value="enterprise.supportprice" />
          </div>
          <div class="form_data">
            <h6>带贫困户数(户)</h6>
            <a-input v-model:value="enterprise.uppoor_num" />
          </div>
          <div class="form_data">
            <h6>贷款银行</h6>
            <a-select
              v-model:value="enterprise.loan_bank"
              style="width: 100%"
              ref="select"
            >
              <a-select-option
                v-for="(item, i) in Loan_banklist"
                :key="i"
                :value="item.bank_name"
              >
                {{ item.bank_name }}
              </a-select-option>
            </a-select>
          </div>
          <div class="form_data">
            <h6>贷款银行详细名称</h6>
            <a-textarea
              v-model:value="enterprise.loan_bank_desc"
              :auto-size="{ minRows: 4 }"
            />
          </div>
          <div class="form_data">
            <h6>信用评级</h6>
            <a-select
              v-model:value="enterprise.credit_rating"
              style="width: 100%"
              ref="select"
            >
              <a-select-option
                v-for="(item, i) in creditlist"
                :key="i"
                :value="item.name"
              >
                {{ item.name }}
              </a-select-option>
            </a-select>
          </div>
          <div class="form_data">
            <h6>贷款模式</h6>
            <a-radio-group
              :options="loanmodel"
              v-model:value="enterprise.loanmodel"
            />
          </div>
          <div class="form_data">
            <h6>贷款类型</h6>
            <a-radio-group
              name="radioGroup"
              v-model:value="enterprise.loan_type"
            >
              <a-radio value="新增"> 新增 </a-radio>
              <a-radio value="续贷"> 续贷 </a-radio>
            </a-radio-group>
          </div>
          <div class="form_data">
            <h6>带贫方式</h6>
            <a-checkbox-group
              v-model:value="enterprise.uppoor_type"
              :options="mannerlist"
              @change="enterprise_uppoor_type"
            />
          </div>
          <div class="form_data">
            <h6>带贫名单</h6>
            <a-upload
              action="http://jinrongapi.yooobei.com/Loan/uploads"
              list-type="picture"
              multiple="true"
              v-model:fileList="povertyList"
              @change="handlepoverty"
            >
              <a-button> <upload-outlined /> 上传 </a-button>
            </a-upload>
          </div>
          <div class="form_data">
            <h6>委托证明</h6>
            <a-upload
              action="http://jinrongapi.yooobei.com/Loan/uploads"
              list-type="picture"
              multiple="true"
              v-model:fileList="proveList"
              @change="handleprove"
            >
              <a-button> <upload-outlined /> 上传 </a-button>
            </a-upload>
          </div>
          <div class="form_btn">
            <a-button
              @click="primary_enterprise"
              style="margin-right: 20px"
              type="primary"
              >确认</a-button
            >
            <a-button @click="Reset">重置</a-button>
          </div>
        </div>
      </a-tab-pane>
    </a-tabs>
  </div>
</template>

<script>
import { UploadOutlined } from "@ant-design/icons-vue";
import { message } from "ant-design-vue";
import maplist from "../assets/json/map.json";
import https from "@/utils/https.js";
export default {
  name: "mobile",
  props: {},
  comments: { UploadOutlined },
  data() {
    return {
      title_left: "个人信息录入",
      title_right: "企业信息录入",
      // 个人逾期原因
      ifoverduereason_information: false,
      // 企业逾期原因
      ifoverduereason_enterprise: false,
      // 地点
      map: [maplist],
      // 信用等级列表
      creditlist: [
        { name: "AAA+" },
        { name: "AAA" },
        { name: "AA" },
        { name: "A" },
        { name: "无信用" },
      ],
      // 逾期列表
      overduereasonlist: [],
      // 银行列表
      Loan_banklist: [],
      // 产业列表
      industry: [],
      // 信用评定列表
      isjoincredit: [],
      // 贷款模式
      loanmodel: [],
      // 带贫方式列表
      mannerlist: [],
      // 个人信息
      information: {
        nickname: "", //贷款人姓名
        area: "", //县
        country: "", //乡
        village: "", //村
        phone: "", //手机号
        idcard: "", //贷款人身份证号
        is_poor: "1", //是否为贫困户
        credit_rating: "AAA+", //信用评级
        loan_starttime: "", //借款日
        loan_endtime: "", //还款日
        payback_time: "", //到期日
        loan_status_type: "正常", //贷款状态
        overduereason: "", //逾期原因
        loan_price: "", //贷款金额
        year_interest_rate: "", //年利率
        discount_rate: "", //贴息利率
        discount_price: "", //应贴息金额
        supportprice: "", // 产业扶持金额
        loan_use: "请选择产业",
        distribution: "", //产业分布类型
        loan_type: "新增", //贷款类型
        loan_bank: "", //贷款银行
        loan_bank_desc: "", //贷款银行详细名称
        is_joincredit: "未参加过", //是否参加过信用等级评定
        is_safe: "否", //是否购买过贷款保证保险
        entrustprove: "", //委托证明
      },
      // 企业信息
      enterprise: {
        area: "", //县
        country: "", //乡
        village: "", //村
        nickname: "", //企业名称
        credit_code: "", //统一社会信用代码
        deputy_username: "", //法定代表人
        phone: "", //联系电话
        address: "", //注册地址
        staff_num: "", //股东人数
        is_faucet: "0", //龙头企业
        loan_price: "", //贷款金额
        loan_starttime: "", //借贷日
        loan_endtime: "", //到期日
        payback_time: "", //还款日
        loan_status_type: "正常", //贷款状态
        overduereason: "", //逾期原因
        year_interest_rate: "", //年利率
        discount_rate: "", //贴息利率
        discount_price: "", //应贴息金额
        loan_use: "请选择产业",
        distribution: "",
        supportprice: "", // 产业扶持金额
        uppoor_num: "", //带贫困户数
        loan_bank: "", //贷款银行
        loan_bank_desc: "", //贷款银行详细信息
        credit_rating: "AAA+", //信用评定
        loanmodel: "四位一体", //贷款模式
        loan_type: "新增", //贷款类型
        uppoor_type: "", //带贫方式
        uppoor_roster: "", //带贫名单
        entrustprove: "", //委托证明
      },
      // 个人上传附件列表
      fileList: [],
      // 企业上传附件列表
      povertyList: [],
      proveList: [],
    };
  },
  created() {
    https
      .fetchPost("/Loan/init")
      .then((res) => {
        console.log(res.data);
        if (res.data.code == 200) {
          this.Loan_banklist = res.data.data.banklist;
          this.industry = res.data.data.purposeloan;
          this.industry[0].children = res.data.data.distribution;
          this.isjoincredit = res.data.data.isjoincredit;
          this.loanmodel = res.data.data.loanmodel;
          this.mannerlist = res.data.data.uppoortype;
          this.overduereasonlist = res.data.data.overduereason;

          this.industry.map((item) => (item.label = item.value));
          this.industry[0].children.map((item) => (item.label = item.value));
          this.isjoincredit.map((item) => (item.label = item.value));
          this.loanmodel.map((item) => (item.label = item.value));
          this.mannerlist.map((item) => {
            item.label = item.value;
            item.value = item.key;
          });
          this.overduereasonlist.map((item) => (item.label = item.value));
        }
      })
      .catch((res) => {
        console.log(res);
      });
  },
  methods: {
    // 个人获取地点
    onChange(value, selectedOptions) {
      console.log(value);
      this.information.area = selectedOptions[0].label;
      this.information.country = selectedOptions[1].label;
      this.information.village = selectedOptions[2].label;
    },
    // 企业获取地点
    onChangx(value, selectedOptions) {
      console.log(value);
      this.enterprise.area = selectedOptions[0].label;
      this.enterprise.country = selectedOptions[1].label;
      this.enterprise.village = selectedOptions[2].label;
    },

    // 个人选择产业
    industrybtn(value, selectedOptions) {
      console.log(selectedOptions);
      this.information.loan_use = selectedOptions[0].label;
      if (selectedOptions[1]) {
        this.information.distribution = selectedOptions[1].label;
      }
    },
    // 企业选择产业选择产业
    enterprisebtn(value, selectedOptions) {
      console.log(selectedOptions);
      this.enterprise.loan_use = selectedOptions[0].label;
      if (selectedOptions[1]) {
        this.enterprise.distribution = selectedOptions[1].label;
      }
    },

    // 带贫方式
    enterprise_uppoor_type(e, index) {
      console.log(e);
      console.log(index);
    },

    // 还款日期
    information_starttime(date, dateString) {
      this.information.loan_starttime = dateString;
    },
    information_endtime(date, dateString) {
      console.log(date);
      this.information.loan_endtime = dateString;
    },
    information_payback(date, dateString) {
      console.log(date, dateString);
      if (this.information.loan_endtime) {
        if (
          Date.parse(this.information.payback_time) <=
          Date.parse(this.information.loan_endtime)
        ) {
          this.information.payback_time = dateString;
          this.ifoverduereason_information = false;
        } else {
          this.ifoverduereason_information = true;
          this.information.payback_time = dateString;
        }
      } else {
        this.information.payback_time = "";
        message.info("请先选择到期日期");
      }
    },

    enterprise_starttime(date, dateString) {
      this.enterprise.loan_starttime = dateString;
    },
    enterprise_endtime(date, dateString) {
      this.enterprise.loan_endtime = dateString;
    },
    enterprise_payback(date, dateString) {
      console.log(date, dateString);
      if (this.enterprise.loan_endtime) {
        if (
          Date.parse(this.enterprise.payback_time) <=
          Date.parse(this.enterprise.loan_endtime)
        ) {
          this.enterprise.payback_time = dateString;
          this.ifoverduereason_enterprise = false;
        } else {
          this.ifoverduereason_enterprise = true;
          this.enterprise.payback_time = dateString;
        }
      } else {
        this.enterprise.payback_time = "";
        message.info("请先选择到期日期");
      }
    },

    // 确认按钮
    primary_personal() {
      console.log(this.information);
      https
        .fetchPost("/Loan/userInsertData", this.information)
        .then((res) => {
          console.log(res);
          if (res.data.code == 200) message.success("信息录入成功");
          else message.error(res.data.msg);
        })
        .catch((res) => {
          console.log(res);
        });
    },
    primary_enterprise() {
      // message.info();
      let uppoor_type = "";
      this.enterprise.uppoor_type.map(
        (item) => (uppoor_type = uppoor_type + item + ",")
      );
      this.enterprise.uppoor_type = uppoor_type;
      console.log(this.enterprise);

      https
        .fetchPost("/Loan/enterInsertData", this.enterprise)
        .then((res) => {
          console.log(res);
          if (res.data.code == 200) message.success("信息录入成功");
          else message.error(res.data.msg);
        })
        .catch((res) => {
          console.log(res);
        });
    },

    // 重置
    Reset() {
      // 个人逾期原因
      this.ifoverduereason_information = false;
      // 企业逾期原因
      this.ifoverduereason_enterprise = false;
      // 个人信息
      this.information = {
        nickname: "", //贷款人姓名
        area: "", //县
        country: "", //乡
        village: "", //村
