        loan_starttime: "", //借款日
        loan_endtime: "", //还款日
        payback_time: "", //到期日
        loan_status_type: "正常", //贷款状态
        overduereason: "", //逾期原因
        loan_price: "", //贷款金额
        year_interest_rate: "", //年利率
        discount_rate: "", //贴息利率
        discount_price: "", //应贴息金额
        supportprice: "", // 产业扶持金额
        loan_use: "请选择产业",
        distribution: "", //产业分布类型
        loan_type: "新增", //贷款类型
