    },

    // 上传附件
    handleChange(info) {
      if (info.file.status !== "done") return;
      console.log(info.fileList[0].response.code);

      if (info.fileList[0].response.code == 200) {
        message.info("上传成功");
        this.information.entrustprove =
          this.information.entrustprove + info.fileList[0].response.data + ",";
      }
    },

    // 上传脱贫名单
    handlepoverty(info) {
      if (info.file.status !== "done") return;
      console.log(info.fileList[0].response.code);

      if (info.fileList[0].response.code == 200) {
        message.info("上传成功");
        this.enterprise.uppoor_roster =
          this.enterprise.uppoor_roster + info.fileList[0].response.data + ",";
      }
    },

    // 上传委托证明
    handleprove(info) {
      if (info.file.status !== "done") return;
      console.log(info.fileList[0].response.code);

      if (info.fileList[0].response.code == 200) {
        message.info("上传成功");
        this.enterprise.entrustprove =
          this.enterprise.entrustprove + info.fileList[0].response.data + ",";
      }
    },

    // 样式
    selected(e) {
      let flag = e;
      let left = document.getElementsByClassName("left")[0];
      let right = document.getElementsByClassName("right")[0];
      if (flag) {
        left.style.width = "100%";
        right.style.width = "0%";
        setTimeout(() => {
          right.style.display = "none";
        }, 300);
        setTimeout(() => {
          left.style.paddingTop = "0px";
          document.getElementsByClassName("form")[0].style.height = "100%";
        }, 1000);
      } else {
        right.style.width = "100%";
        left.style.width = "0%";
        setTimeout(() => {
          document.getElementsByClassName("title")[0].style.display = "none";
        }, 300);
        setTimeout(() => {
          left.style.display = "none";
        }, 900);
        setTimeout(() => {
          right.style.paddingTop = "0px";
          document.getElementsByClassName("form")[1].style.height = "100%";
        }, 1000);
      }
    },
  },
};
</script>

